export default {
  namespaced: true,

  state: {
    showAnimationBeforeEnter: true, // 
    isAnimateOver: false, // 
    isSkipAnimate: false, // 
    isStepIntoMC: false, // MC
    isStepIntoBG: false // 
  },
  mutations: {
    doneAnimate (state) {
      state.isAnimateOver = true
    },
    skipAnimate (state) {
      state.isAnimateOver = true
      state.isStepIntoBG = true
      state.isSkipAnimate = true
    },
    animateMC (state) {
      state.stepIntoMC = true
    },
    stepIntoBGWall (state) {
      state.stepIntoBG = true
    },
    doneWelcome (state) {
      state.showAnimationBeforeEnter = false
    }
  },
  actions: {},
  getters: {

  }
}
